import React, { FC } from 'react';
import { isMobile } from 'react-device-detect';
// @ts-ignore
import Tone from 'tone';
import './styles.css';

export interface IKeyProps {
  note: string;
  color?: string;
  style?: object;
  onKeyPress(): void;
  onKeyRelease(): void;
}

const Key: FC<IKeyProps> = props => {
  const { color, style } = props;

  const synth = new Tone.Synth().toMaster();

  const handleKeyPress = () => {
    const key = props.note;
    console.log(key);
    synth.triggerAttack(key);
  };

  const handleKeyRelease = () => {
    synth.triggerRelease();
  };
  return isMobile ? (
    <div className={`key ${color}`} style={style} onTouchStart={handleKeyPress} onTouchEnd={handleKeyRelease}></div>
  ) : (
    <div className={`key ${color}`} style={style} onMouseDown={handleKeyPress} onMouseUp={handleKeyRelease}></div>
  );
};

export default Key;
